.goodsAdmin{
    height: 100%;
    width: 100%;
    .content{
        height: 100%;
        width: 100%;
        background: #fff;
        margin: 20px 0 0 20px;
        overflow: hidden;
        .contentTop{
            padding: 20px;
            border-bottom:1px solid #eee; 
            font-size: 20px
        }
        .contentBottom{
            padding: 20px;
                .detail-left {
                  font-size: 20px;
                  font-weight: bold;
                  margin-right: 10px;
                }
                .detail-img {
                  width: 150px;
                  height: 150px;
                  border: 1px solid black;
                  margin-right: 10px;
                }
        }
    }
}